<template>
  <div>
    <el-table
      :data="tableData"
      style="width: 100%; margin-bottom: 20px"
      row-key="id"
      border
      :tree-props="{ children: 'children' }"
    >
      <el-table-column prop="id" label=""> </el-table-column>
      <el-table-column prop="title" label=""> </el-table-column>
      <el-table-column label="">
        <template v-slot="prop">
          <img :src="$imgHead + prop.row.img" alt="" />
        </template>
      </el-table-column>
      <el-table-column prop="status" label="">
        <template v-slot="prop">
          <el-button type="success" v-if="prop.row.status == 1"></el-button>
          <el-button type="info" v-else></el-button>
        </template>
      </el-table-column>
      <el-table-column prop="status" label="" width="200px">
        <template v-slot="prop">
          <el-button type="primary" @click="edit(prop.row.id)"></el-button>
          <el-button type="danger" @click="del(prop.row.id)"></el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import { successAlert, warningAlert } from "../../../utils/alter";
import { editBanner, delBanner } from "../../../utils/request";
export default {
  data() {
    return {};
  },
  methods: {
    edit(id) {
      this.$emit("edit", id);
    },
    del(id) {
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      })
        .then(() => {
          // 
          delBanner({ id }).then((res) => {
            // 
            successAlert(res.data.msg);
            this.requestBannerList();
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "",
          });
        });
    },
    ...mapActions({
      requestBannerList: "banner/bannerListActions",
    }),
  },
  computed: {
    ...mapGetters({
      tableData: "banner/bannerList",
    }),
  },
  mounted() {
    this.requestBannerList();
  },
};
</script>

<style>
img {
  width: 100px;
}
</style>


