<template>
  <div>
    <el-table
      :data="tableData"
      style="width: 100%; margin-bottom: 20px"
      row-key="id"
      border
      :tree-props="{ children: 'children' }"
    >
      <el-table-column prop="id" label=""> </el-table-column>
      <el-table-column prop="rolename" label=""> </el-table-column>
      <el-table-column prop="status" label="">
        <template v-slot="prop">
          <el-button type="success" v-if="prop.row.status == 1"></el-button>
          <el-button type="info" v-else></el-button>
        </template>
      </el-table-column>
      <el-table-column prop="status" label="" width='200px'>
        <template v-slot="prop">
          <el-button type="primary" @click="edit(prop.row.id)"></el-button>
          <el-button type="danger" @click="del(prop.row.id)"></el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import {mapGetters,mapActions} from 'vuex'
import {delRole} from '../../../utils/request'
import {successAlert} from '../../../utils/alter'
export default {
    mounted(){
        // 
        this.requestRoleList();
    },
    methods:{
        ...mapActions({
            'requestRoleList':'role/roleListActions'
        }),
        edit(id){
          // console.log(id);
          this.$emit('edit',id);
        },
        del(id){
          // this.$emit('del',id);
          this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          // 
          delRole({id}).then(res=>{
              // 
              successAlert(res.data.msg);
              this.requestRoleList()
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
        }
    },
    computed:{
        ...mapGetters({
            "tableData":"role/roleList"
        })
    }
};
</script>

<style>
</style>


