import {getVip} from '../../utils/request'
const state = {
  vipList:[],//
}

// 
const mutations = {
  changeVipList(state,arr){
    state.vipList = arr
  }
}

const actions = {
  vipListActions(context){
    // 
    getVip().then(res=>{
      context.commit('changeVipList',res.data.list);
    })
  }
}

const getters = {
  vipList(state){
    return state.vipList
  }
}

export default {
  state,
  mutations,
  actions,
  getters,
  namespaced:true,
}



