<template>
  <el-table
    :data="tableData"
    style="width: 100%; margin-bottom: 20px"
    row-key="id"
    border
    :tree-props="{ children: 'children' }"
  >
    <el-table-column prop="title" label=""></el-table-column>
    <el-table-column prop="status" label="">
      <template v-slot="prop">
        <el-button type="success" v-if="prop.row.status == 1"></el-button>
        <el-button type="info" v-else></el-button>
      </template>
    </el-table-column>
    <el-table-column label="" width="200px">
      <template v-slot="prop">
        <el-button type="primary" @click="edit(prop.row.id)"></el-button>
        <el-button type="danger" @click="del(prop.row.id)"></el-button>
      </template>
    </el-table-column>
  </el-table>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import { delSecond } from "../../../utils/request";
import { successAlert, warningAlert } from "../../../utils/alter";
export default {
  data() {
    return {};
  },
  methods: {
    edit(id) {
      this.$emit("edit", id);
    },
    del(id) {
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      })
        .then(() => {
          // 
          delSecond({ id }).then((res) => {
            // 
            successAlert(res.data.msg);
            this.requestSecond();
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "",
          });
        });
    },
    ...mapActions({
      requestSecond: "second/secondListActions",
    }),
  },
  computed: {
    ...mapGetters({
      tableData: "second/secondList",
    }),
  },
  mounted() {
    this.requestSecond();
  },
};
</script>

<style>
</style>


