<template>
  <div>
     <div id="main"></div>
  </div>
</template>

<script>
import echarts from 'echarts';
export default {
  mounted(){
    // domecharts
        var myChart = echarts.init(document.getElementById('main'));

        // 
        var option = {
            title: {
                text: ''
            },
            tooltip: {},
            legend: {
                data:['']
            },
            xAxis: {
                data: ["","","","","",""]
            },
            yAxis: {},
            series: [{
                name: '',
                type: 'bar',
                data: [200, 200, 203, 230, 500, 300]
            }]
        };

        // 
        myChart.setOption(option);
  }

}
</script>

<style>
#main{
  width:600px;
  height: 400px;
  margin:20px auto;
}
</style>


